import { css } from 'lit';

export const styles = css`:host {
  --elevation: var(--ig-button-group-elevation, var(--ig-elevation-0));
  --idle-shadow-color: var(--ig-button-group-idle-shadow-color, transparent);
  --item-background: var(--ig-button-group-item-background, hsla(var(--ig-gray-50), var(--ig-gray-a)));
  --item-border-color: var(--ig-button-group-item-border-color, hsla(var(--ig-gray-400), var(--ig-gray-a)));
  --item-text-color: var(--ig-button-group-item-text-color, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --item-hover-text-color: var(--ig-button-group-item-hover-text-color, hsla(var(--ig-primary-200), var(--ig-primary-a)));
  --item-hover-background: var(--ig-button-group-item-hover-background, hsla(var(--ig-gray-50), var(--ig-gray-a)));
  --item-selected-text-color: var(--ig-button-group-item-selected-text-color, hsla(var(--ig-primary-200), var(--ig-primary-a)));
  --item-selected-background: var(--ig-button-group-item-selected-background, hsla(var(--ig-gray-200), var(--ig-gray-a)));
  --item-selected-hover-background: var(--ig-button-group-item-selected-hover-background, hsla(var(--ig-gray-200), var(--ig-gray-a)));
  --item-selected-border-color: var(--ig-button-group-item-selected-border-color, hsla(var(--ig-gray-500), var(--ig-gray-a)));
  --disabled-text-color: var(--ig-button-group-disabled-text-color, hsla(var(--ig-gray-400), var(--ig-gray-a)));
  --disabled-background-color: var(--ig-button-group-disabled-background-color, hsla(var(--ig-gray-50), var(--ig-gray-a)));
  --item-disabled-border: var(--ig-button-group-item-disabled-border, hsla(var(--ig-gray-400), var(--ig-gray-a)));
  --border-radius: var(--ig-button-group-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0.1) * 1.25rem), 1.25rem));
}
:host [part~=toggle] {
  --ig-icon-size: 1rem;
  --igx-icon-size: 1rem;
  min-height: max(var(--is-large, 1) * max(2.375rem, -1 * 2.375rem), var(--is-medium, 1) * max(2rem, -1 * 2rem), var(--is-small, 1) * max(1.5rem, -1 * 1.5rem));
  border-width: 0.0625rem;
  border-color: var(--item-border-color);
  color: var(--item-text-color);
  background: var(--item-background);
  padding-inline: max(var(--is-large, 1) * max(1rem, -1 * 1rem) * var(--ig-spacing-inline-large, var(--ig-spacing-inline, --ig-spacing)), var(--is-medium, 1) * max(1rem, -1 * 1rem) * var(--ig-spacing-inline-medium, var(--ig-spacing-inline, --ig-spacing)), var(--is-small, 1) * max(1rem, -1 * 1rem) * var(--ig-spacing-inline-small, var(--ig-spacing-inline, --ig-spacing)));
  padding-block: max(var(--is-large, 1) * max(0.4375rem, -1 * 0.4375rem) * var(--ig-spacing-block-large, var(--ig-spacing-block, --ig-spacing)), var(--is-medium, 1) * max(0.4375rem, -1 * 0.4375rem) * var(--ig-spacing-block-medium, var(--ig-spacing-block, --ig-spacing)), var(--is-small, 1) * max(0.4375rem, -1 * 0.4375rem) * var(--ig-spacing-block-small, var(--ig-spacing-block, --ig-spacing)));
}

:host(:hover) [part~=toggle],
:host(:focus) [part~=toggle] {
  color: var(--item-hover-text-color);
  background: var(--item-hover-background);
}

:host([selected]) [part~=toggle] {
  color: var(--item-selected-text-color);
  background: var(--item-selected-background);
  border-color: var(--item-selected-border-color);
}

:host([selected]:hover) [part~=toggle],
:host([selected]:focus) [part~=toggle] {
  color: var(--item-selected-text-color);
  background: var(--item-selected-hover-background);
}

:host([disabled]) [part~=toggle],
:host(:disabled) [part~=toggle] {
  color: var(--disabled-text-color);
  background: var(--disabled-background-color);
  border-color: var(--item-disabled-border);
}`;
